*{box-sizing:border-box}table{width:100%}table th{text-align:left;border-bottom:1px solid #ccc}table th,table td{padding:.4em}table.fold-table>tbody>tr.view td,table.fold-table>tbody>tr.view th{cursor:pointer}table.fold-table>tbody>tr.view td:first-child,table.fold-table>tbody>tr.view th:first-child{position:relative;padding-left:20px}table.fold-table>tbody>tr.view td:first-child:before,table.fold-table>tbody>tr.view th:first-child:before{position:absolute;top:50%;left:5px;width:9px;height:16px;margin-top:-8px;font:16px fontawesome;color:#999;content:"\f0d7";transition:all .4s ease}table.fold-table>tbody>tr.view:nth-child(4n-1){background:#eee}table.fold-table>tbody>tr.view:hover{background:#ccc}table.fold-table>tbody>tr.view.open{transition:all .2s ease;background:#161625;color:white}table.fold-table>tbody>tr.view.open td:first-child:before,table.fold-table>tbody>tr.view.open th:first-child:before{-webkit-transform:rotate(-180deg);transform:rotate(-180deg);color:#00ff15}table.fold-table>tbody>tr.fold{display:none}table.fold-table>tbody>tr.fold.open{display:table-row;transition:all .6s ease}.fold-content{padding:.5em}.fold-content h3{margin-top:0}.fold-content>table>tbody tr:nth-child(even){background:#eee}

.card-container {
  display: flex;
  justify-content: center;
  margin-top: 30px;
}

